<template>
  <!-- Vuetify -->
  <v-app>

    <!-- ツールバー -->
    <v-app-bar app color="blue" dark elevation="2" dense
			src="https://cdn.vuetifyjs.com/images/backgrounds/vbanner.jpg"
		>
      <v-toolbar-title class="pa-2 ma-0">Domemo</v-toolbar-title>
        <div v-if="!showNameInput" class="pa-2 ma-0">
          <v-btn color="cyan darken-3" class="white--text" tile depressed>ルーム</v-btn>
          <v-btn outlined color="white" class="mr-3" tile depressed>{{room_name}}</v-btn>
          <v-btn color="cyan darken-3" class="white--text" tile depressed>プレーヤー</v-btn>
          <v-btn outlined color="white" tile depressed>{{player_name}}</v-btn>
        </div>
    </v-app-bar>

    <v-container>
      <v-row class="pa-0">
        <v-col class="pt-0 px-3">
          <div v-if="showNameInput">
            <v-form>
              <v-text-field v-model="player_name" label="プレーヤー名を入れて">
                <v-btn color="cyan" dark class="my-1 mr-4 p-1" @click="send()" slot="append">ログイン
                  <v-icon right dark>mdi-login-variant</v-icon>
                </v-btn>
              </v-text-field>
            </v-form>
          </div>
        </v-col>
      </v-row>

      <!-- ゲーム開始 -->
      <div v-if="showStartGame">
        <v-row no-gutters class="ma-3">
          <v-col align-h="start">
              <v-btn v-on:click="start" color="success">ゲーム開始</v-btn>
          </v-col>
        </v-row>
      </div>

      <!-- カード表示 -->
      <div v-for="(n,idx) in oth_name" v-bind:key="idx" class="bg-image-green-cloth">
        <v-row justify="start">
          <v-col>
            <v-btn v-if="player_name==n"
              color="teal lighten-2" class="mt-1 py-0" dark depressed tile slot="prepend" block>
                <v-icon right dark class="mr-2">mdi-account</v-icon>
                自分
            </v-btn>
            <v-btn v-else-if="n == '場'"
              v-btn color="indigo lighten-2" class="mt-1 py-0" dark depressed tile slot="prepend" block>
                場
            </v-btn>
            <v-btn v-else
              color="teal lighten-2" class="mt-1 py-0" dark depressed tile slot="prepend" block>
                <v-icon right dark class="mr-2">mdi-account</v-icon>
                {{n}}
            </v-btn>
          </v-col>
        </v-row>
        <v-row class="ml-5">
          <div v-for="(value,idxc) in oth_cards[idx]" v-bind:key="idxc">
            <v-card color="grey darken-3" dark class="px-2 pt-2 pb-0 mr-2" align="center">
              <p class="text-h4 font-weight-bold">{{value}}</p>
            </v-card>
          </div>
        </v-row>
      </div>

      <!-- カード選択 -->
      <div v-if="showNameInput != true">
        <!-- hit メッセージ-->
        <div v-if="showMessage == true">
          <v-card v-bind:color="hit_message_color" class="mt-3 pa-1" block>
            <v-card-text class="white--text text-h5">
              <div>プレーヤー({{hit_player}})の選んだ数字は{{hit_message}}です</div>
              <p class="text-h4">{{hit_number}}</p>
            </v-card-text>
          </v-card>
        </div>
        <div v-if="myTurn == true">
          <v-row no-gutters class="mt-3" align-h="start">番号を選択してください</v-row>
          <v-row no-gutters class="mt-1" align-h="center">
            <v-card outlined class="pa-2">
              <v-btn-toggle v-model="num_selected" tile color="deep-purple accent-3" group>
                <v-btn v-for="btn in 7" :key="btn.id"
                  class="text-h5 font-weight-bold" fab>
                  {{btn}}
                </v-btn>
              </v-btn-toggle>
              <v-btn v-on:click="select_number()" color="deep-orange" dark
                class="ml-3">決定</v-btn>
            </v-card>
          </v-row>
        </div>
        <div v-else>
          <v-card color="deep-orange" class="white--text text-h5 mt-3 pa-3" block>{{othTurnString}}</v-card>
        </div>
        <div v-if="showWinner == true">
          <v-card color="blue" class="mt-3 pa-1" block>
            <v-card-text class="white--text text-h5">
              <v-btn v-on:click="again_game()" color="success" class="mr-3 pa-2">もう一度やる<br>(同じ参加者)</v-btn>
              <v-btn v-on:click="reset_game()" color="red darken-3" class="pa-2" dark>
                ログインへ戻る<br>(参加者を変える)</v-btn>
            </v-card-text>
          </v-card>
        </div>
        <!-- v-row>
          <v-col>
            <v-form>
              <v-text-field v-model="input" label="メッセージ">
                <v-btn color="cyan" dark @click="send()" class="mr-4 p-1" slot="append">送る
                  <v-icon right dark>mdi-send</v-icon>
                </v-btn>
              </v-text-field>
            </v-form>
          </v-col>
        </v-row -->
      </div><!-- end of v-if showNameInput -->
      <v-row>
        <v-col>
          <v-card>
            <v-card-text>
              <p class="text--primary text-left">ステータス</p>
              <p v-html=outMessage class="text-left">{{outMessage}}</p>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-btn v-on:click="reset_game()" color="red darken-3"
            block class="white--text">ゲームをリセットする</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
//import NumberCard from './components/NumberCard.vue';

export default {
  name: 'App',
  components: {
 //   NumberCard
  },
  data: () => ({
    input: "",
    outMessage: "",
    room_name: "",
    player_name: "",
    status : "init",
    socket: "",
    oth_cards: [],
    oth_name: [],
    cards_table: [],
    // 番号選択
    num_selected: "",
    num_options: [],
    // hit
    hit_player: "",
    hit_number: "",
    hit_message: "",
    hit_message_color: "blue darken-3",
    hit_show_interval: 3000, // 5秒
    //表示制御系
    showNameInput: true,
    showStartGame: false,
    myTurn: false,
    othTurnString: "",
    showMessage: false,
    showWinner: false
  }),
  created: function(){

    //  イベントリスナーからVueコンポーネントに値を渡すために一度selfで変数化しておく
    const self = this;

    self.socket = new WebSocket("wss://s6016qlo58.execute-api.ap-northeast-1.amazonaws.com/Prod");

    self.socket.onopen = function() {
        self.outMessage = "接続できました！<br>";
    };

    self.socket.onmessage = async function(e) {

        var d = JSON.parse(e.data);
        //console.log("d.data:" + d.data );

        var s = "";
        s += 'response:' + d.response 
            + '/ status:' + d.status 
            + '/ turn:' + d.turn
            + '/ data:' + JSON.stringify(d.data)
            + '<br>';
        if(d.response == "reset"){
          console.log('reset game')
          window.location.reload();
        }
        if(d.status == 'waitJoin'){
            s += d.room_name + "へ入りました。";
            self.showStartGame = true;
            self.room_name = d.room_name;
        }
        else if( d.response == 'deal'){
          // d.status == 'battle' から変更

          var oth = JSON.parse(d.data);
          console.log('oth:' + JSON.stringify(oth));

          self.oth_name = [];
          self.oth_cards = [];
          var sn = "",sc;
          var on,oc;
          for ( [on, oc] of Object.entries(oth)) {
            console.log("name is " + on + ", cards is " + oc);
            if( on != self.player_name ){
              if( on == "_table_cards_"){
                self.oth_name.push("場");
              }
              else{
                self.oth_name.push(on);
              }
              self.oth_cards.push(oc);
            }
            else {
              sn = on;
              sc = oc;
            }
          }
          if( sn != ""){
            self.oth_name.push(sn);
            self.oth_cards.push(sc);
          }
          self.showNameInput = false;
          self.showStartGame = false;

          if( d.status == 'finish'){
            console.log( 'Game is finished. Winner is ' + d.winner );
            self.status = d.status;
            s += 'ゲーム終了<br>'
            self.outMessage = s + "<br>" + self.outMessage;

            self.othTurnString = d.winner + 'の勝ち'
            self.myTurn = false;
            // 「もう一度やる」「ログインへ戻る」の選択肢を表示する
            self.showWinner = true;

            return
          }

          // ターン情報処理
          var tdat = JSON.parse(d.turn)
          console.log('turn data:' + JSON.stringify(tdat));

          var tn,tv;
          for ( [tn, tv] of Object.entries(tdat)) {
            
            console.log( 'name :' + tn + ' value :' + tv['S']);

            if( tn == self.player_name && tv['S'] == 'turn'){
              console.log( 'my turn');
              self.myTurn = true;
              self.othTurnString = '';
              break;
            }
            else {
              self.myTurn = false;
              if( tv['S'] == 'turn'){
                console.log( tn + ' turn');
                self.othTurnString = tn + 'の順番です';
              }
            }
          }
        }
        // hit応答
        else if( d.response == 'select'){
            console.log('data:' + d.data);
            var h = JSON.parse(d.data)  
            console.log('hit data:' + JSON.stringify(h));

            self.hit_player = h['name'];
            self.hit_number = h['number'];
            console.log("hit name:" + self.hit_player + " number:" + self.hit_number);

          if( d.status == 'hit'){
            self.hit_message = "ヒット";
            self.hit_message_color = "light-green darken-1";
          } else {
            self.hit_message = "はずれ";
            self.hit_message_color = "red darken-3";
          }
          self.showMessage = true;
          await self.sleep(self.hit_show_interval);
          self.showMessage = false;

          if( self.player_name == h['name']){
            if( d.status == 'hit'){
              // カード捨てる
              // hit_player == 自分 だったら、当たったカードを捨てる。
              self.discard( h['name'], h['number']);
            }
            else {
              // TURN変更を呼ぶ
              self.changeTurn();
            }
          }
        }
        else if( d.response == 'turnChanged'){
          console.log('turn changed');

          // ターン情報処理
          var tcd = JSON.parse(d.turn)
          console.log('-->turn data:' + JSON.stringify(tcd));

          var tcn,tcv;
          for ( [tcn, tcv] of Object.entries(tcd)) {
            
            console.log( '-->name :' + tcn + ' value :' + tcv);

            if( tcn == self.player_name && tcv == 'turn'){
              console.log( '-->my turn');
              self.myTurn = true;
              self.othTurnString = '';
              break;
            }
            else {
              self.myTurn = false;
              if( tcv == 'turn'){
                console.log( '-->' + tcn + ' turn');
                self.othTurnString = tcn + 'の順番です';
              }
            }
          }
        }

        self.status = d.status;
        self.outMessage = s + "<br>" + self.outMessage;
    };

    for( let i = 0; i < 7; i++ ){
      self.num_options[i] = i + 1; 
    }
  },
  methods:{
    send: function() {
        this.socket.send(JSON.stringify(
            {
                "action":"joinGame",
                "name": this.player_name,
                "data": this.input
            }
        ));
        this.input = "";
    },
    start: function() {
        this.socket.send(JSON.stringify(
            {
                "action":"gameControl",
                "name": this.player_name,
                "data": this.input,
                "command" : "start"
            }
        ));
        this.input = "";
    },
    reset_game: function() {
        this.socket.send(JSON.stringify(
            {
                "action":"gameControl",
                "name": this.player_name,
                "data": this.input,
                "command" : "resetGame"
            }
        ));
        this.input = "";
    },
    select_number: function() {
        this.socket.send(JSON.stringify(
            {
                "action":"gameControl",
                "name": this.player_name,
                "data": this.num_selected + 1,
                "command" : "select"
            }
        ));
        this.input = "";
    },
    discard: function( name, num ) {
      this.socket.send(JSON.stringify(
          {
              "action":"gameControl",
              "name": name,
              "data": num,
              "command" : "discard"
          }
      ));
      this.input = "";
    },
    changeTurn: function() {
      this.socket.send(JSON.stringify(
          {
              "action":"gameControl",
              "name": this.player_name,
              "data": "",
              "command" : "changeTurn"
          }
      ));
      this.input = "";
    },
    again_game: function(){
      this.start();
      this.showWinner = false;
    },
    sleep: function(msec) {
      return new Promise(function(resolve) {
 
        setTimeout(function() {resolve()}, msec);
      })
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.bg-image-green-cloth {
	background-image: url('~@/assets/green_cloth.jpg');
  background-size: cover;
}
</style>